import { _decorator, Component, Node, Label, SpriteFrame } from 'cc';
import { GameScene } from '../GameScene';
import { ChoicesModal } from './Modal/ChoicesModal';
import { QuestionModal } from './Modal/QuestionModal';
import { ResultModal } from './Modal/ResultModal';
const { ccclass, property } = _decorator;

const QuizType = {
    None    : 'None',
    Gesture : 'Gesture',
    Act     : 'Act',
    Quiz    : 'Quiz',
} as const;

export type QuizType = typeof QuizType[keyof typeof QuizType];

@ccclass('QuizData')
export class QuizData{
    mIndex : number;
    mAnswer : number;
}

@ccclass('QuizComponent')
export abstract class QuizComponent extends Component {


    @property(QuestionModal)
    questionModal : QuestionModal = null;
    @property(ChoicesModal)
    choicesModal : ChoicesModal = null;
    @property(ResultModal)
    resultModal : ResultModal = null;

    mType : QuizType;
    mNumber : number;
    mSentence : string;
    abstract mData : QuizData;

    public abstract Initialize() : void;
    public abstract SetQuiz() : void;

    public Reset(){
        this.mType = 'None';
        this.mNumber = 0;
        this.mSentence = "";
    }

    public GetQuizData():QuizData{
        return this.mData;
    }
}

